import React, {useState} from 'react'
import './style.css'
import {connect} from 'react-redux'
import { bindActionCreators } from 'redux'
import { 
    appCarregando,
 } from '../../redux/actions'
import Header from '../layout/Header'
import Footer from '../layout/Footer'
import { Table } from 'react-bootstrap'
import Manutencao from '../manutencao'

class Historico extends React.Component {
    state = {
        loading: false,
        registros :[
        {
            id: this.props.id,
            criadoEm: this.props.criadoEm,
            ordem: this.props.newCopia,
        },
        {
            id: Math.floor(Math.random() * 100000 + 100000),
            criadoEm: new Date().toLocaleString(),
            ordem: 8
        }
    ]
    };

    inputChangeNome = event => {
        this.setState({ inputNome: event.target.value })
    }
    inputChangeBanca = event => {
        this.setState({ inputBancaInicial: event.target.value })
    }
    
    render(){
            return (
                <div class="historico">
                    <Header />
                    {/* 
                    <p>Histórico</p>
                    <p>R$ {  this.props.newBanca.toFixed(2) } </p>
                    <Table>                       
                            <tr>
                                <td>Registro</td>
                                <td>Retorno</td>
                            </tr>
                            {this.state.registros.map((post) =>
                                <tr key={post.id}>
                                    <td>{post.criadoEm}</td>
                                    <td>R$ {post.ordem}</td>
                                </tr>
                            )}
                    </Table>
                    */}
                    <Manutencao />
                    <Footer />
                </div>            
            );
        }
    }

    const mapStateToProps = state => ({
        newBanca: state.CalculosReducers.newBanca,
        id: state.CalculosReducers.id,
        criadoEm: state.CalculosReducers.criadoEm,
        newCopia: state.CalculosReducers.newCopia
    })
    const mapDispatchToProps = dispatch => bindActionCreators({        
        appCarregando,
    }, dispatch);
    export default connect(mapStateToProps, 
        mapDispatchToProps
    )(Historico); 