import React from 'react'
import './style.css'
import {connect} from 'react-redux'
import { bindActionCreators } from 'redux'
import { 
    atualizaBanca,
    atualizaLucro,
 } from '../../../redux/actions'

class CalculaSoros extends React.Component {
    state = {
        currentTime: new Date().toLocaleString()
    }
    copiaValor = (text) => {
        var textArea = document.createElement("textarea");
        textArea.value = text;
        document.body.appendChild(textArea);
        textArea.select();
        try {
            var successful = document.execCommand('copy');
            var msg = successful ? 'successful' : 'unsuccessful';
            console.log('Copying text command was ' + msg);
        } catch (err) {
            console.log('Oops, unable to copy');
            window.prompt("Copie para área de transferência: Ctrl+C e tecle Enter", text);
        }
        document.body.removeChild(textArea);
    }
    retorno(){
        let temp = parseFloat(this.props.newOrdem) * parseFloat(this.props.newPayout/100);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno2(){
        let temp = parseFloat(this.ordem2()) * parseFloat(this.props.newPayout/100);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno3(){  
        let temp = parseFloat(this.ordem3()) * parseFloat(this.props.newPayout/100);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno4(){  
        let temp = parseFloat(this.ordem4()) * parseFloat(this.props.newPayout/100);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    retorno5(){  
        let temp = parseFloat(this.ordem5()) * parseFloat(this.props.newPayout/100);
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordem2(){
        let temp = parseFloat(this.props.newOrdem) + parseFloat(this.props.newOrdem * this.props.newPayout) / 100;
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordem3(){
        let ordem = this.ordem2();
        let retorno = this.retorno2();
        var temp = ordem + retorno;
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordem4(){
        let ordem = this.ordem3();
        let retorno = this.retorno3();
        var temp = ordem + retorno;
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    ordem5(){
        let ordem = this.ordem4();
        let retorno = this.retorno4();
        var temp = ordem + retorno;
        var calc = parseFloat(temp.toFixed(2));
        return calc;
    }
    _Win = async () => {
        const { atualizaBanca, atualizaLucro, newLucro, newBanca } = this.props;
        

        let inputValue = this.retorno();
        await atualizaBanca({ inputValue, newBanca });
        await atualizaLucro({ inputValue, newLucro });
    }
    _Loss = async () => {
        const { atualizaBanca, atualizaLucro, newLucro, newBanca, newOrdem } = this.props;
        let inputValue = -newOrdem;
        await atualizaBanca({ inputValue, newBanca });
        await atualizaLucro({ inputValue, newLucro });
    }
    _Win2 = async () => {
        const { atualizaBanca, atualizaLucro, newLucro, newBanca } = this.props;
        let inputValue = this.retorno2();
        await atualizaBanca({ inputValue, newBanca });
        await atualizaLucro({ inputValue, newLucro });
    }
    _Loss2 = async () => {
        const { atualizaBanca, atualizaLucro, newLucro, newBanca } = this.props;
        let inputValue = -this.ordem2();
        await atualizaBanca({ inputValue, newBanca });
        await atualizaLucro({ inputValue, newLucro });
    }
    _Win3 = async () => {
        const { atualizaBanca, atualizaLucro, newLucro, newBanca } = this.props;
        let inputValue = this.retorno3();
        await atualizaBanca({ inputValue, newBanca });
        await atualizaLucro({ inputValue, newLucro });
    }
    _Loss3 = async () => {
        const { atualizaBanca, atualizaLucro, newLucro, newBanca } = this.props;
        let inputValue = -this.ordem3();
        await atualizaBanca({ inputValue, newBanca });
        await atualizaLucro({ inputValue, newLucro });
    }
    _Win4 = async () => {
            const { atualizaBanca, atualizaLucro, newLucro, newBanca } = this.props;
            let inputValue = this.retorno4();
            await atualizaBanca({ inputValue, newBanca });
            await atualizaLucro({ inputValue, newLucro });
        }
    _Loss4 = async () => {
        const { atualizaBanca, atualizaLucro, newLucro, newBanca } = this.props;
        let inputValue = -this.ordem4();
        await atualizaBanca({ inputValue, newBanca });
        await atualizaLucro({ inputValue, newLucro });
    }
    _Win5 = async () => {
            const { atualizaBanca, atualizaLucro, newLucro, newBanca } = this.props;
            let inputValue = this.retorno5();
            await atualizaBanca({ inputValue, newBanca });
            await atualizaLucro({ inputValue, newLucro });
        }
    _Loss5 = async () => {
        const { atualizaBanca, atualizaLucro, newLucro, newBanca } = this.props;
        let inputValue = -this.ordem5();
        await atualizaBanca({ inputValue, newBanca });
        await atualizaLucro({ inputValue, newLucro });
    }
    render(){
        return (            
                <div class="tableResultado">
                   <table>
                        <tr>
                            <td> </td>
                            <td class="tituloTabelaLinha">
                                <p>1° Ordem</p>
                            </td>
                            <td class="tituloTabelaLinha">
                                <p>Retorno</p>
                            </td>
                            <td class="resultado">
                                <p>Resultado</p>
                            </td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                                <p>1° Nível</p>
                            </td>
                            <td><div class="campo">
                                R$ { this.props.newOrdem }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor(this.props.newOrdem)} />
                                </div></td>
                            <td><div class="campo">
                                    R$ { this.retorno() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor( this.retorno())} />
                                </div></td>
                            <td>
                                <div class="campo">
                                    <input class="buttonWin" value='Win' type='submit'
                                        onClick={this._Win} />
                                    <input class="buttonLoss" value='Loss' type='submit'
                                        onClick={this._Loss} />
                                </div></td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                            <p>2° Nível</p>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordem2() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor( this.ordem2())} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.retorno2() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor( this.retorno2())} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                    <input class="buttonWin" value='Win' type='submit'
                                        onClick={this._Win2} />
                                    <input class="buttonLoss" value='Loss' type='submit'
                                        onClick={this._Loss2} />
                                </div></td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                            <p>3° Nível</p>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordem3() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor( this.ordem3())} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.retorno3() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor( this.retorno3())} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                    <input class="buttonWin" value='Win' type='submit'
                                        onClick={this._Win3} />
                                    <input class="buttonLoss" value='Loss' type='submit'
                                        onClick={this._Loss3} />
                                </div></td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                            <p>4° Nível</p>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordem4() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor( this.ordem4())} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.retorno4() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor( this.retorno4())} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                    <input class="buttonWin" value='Win' type='submit'
                                        onClick={this._Win4} />
                                    <input class="buttonLoss" value='Loss' type='submit'
                                        onClick={this._Loss4} />
                                </div></td>
                        </tr>
                        <tr>
                            <td class="tituloTabelaColuna">
                            <p>5° Nível</p>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.ordem5() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor( this.ordem5())} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                R$ { this.retorno5() }
                                    <input
                                        value='Copy'
                                        type='submit'
                                        onClick={() => this.copiaValor( this.retorno5())} />
                                </div>
                            </td>
                            <td>
                                <div class="campo">
                                    <input class="buttonWin" value='Win' type='submit'
                                        onClick={this._Win5} />
                                    <input class="buttonLoss" value='Loss' type='submit'
                                        onClick={this._Loss5} />
                                </div></td>
                        </tr>
                    </table>

                </div>
            )
        }
    }

const mapStateToProps = state => ({
    newOrdem:  state.CalculosReducers.newOrdem,
    newPayout:  state.CalculosReducers.newPayout,
    newBanca:  state.CalculosReducers.newBanca,
    newLucro:  state.CalculosReducers.newLucro
})
const mapDispatchToProps = dispatch => bindActionCreators({
    atualizaBanca,
    atualizaLucro
}, dispatch);
export default connect(mapStateToProps, 
    mapDispatchToProps
)(CalculaSoros); 