import React from 'react'
import './style.css'
import {connect} from 'react-redux'
import { bindActionCreators } from 'redux'
import { 
    appCarregando,
    depositeiBanca,
    saqueiBanca,
    zerarDadosCalculos,
    recuperaNome,
 } from '../../redux/actions'
import Header from '../layout/Header'
import Footer from '../layout/Footer'

class Teste extends React.Component {

    state = {
        loading: false,
        inputBancaInicial:0,
        inputDeposito: 0,
        inputSaque: 0,
    }   
    inputChangeBancaInicial = event => {
        this.setState({ inputBancaInicial: event.target.value })
    }
    inputChangeDeposito = event => {
        this.setState({ inputDeposito: event.target.value })
    }
    inputChangeSaque = event => {
        this.setState({ inputSaque: event.target.value })
    }
    _depositei = () => {
        const { inputDeposito } = this.state;
        const { depositeiBanca, newBanca } = this.props;
        depositeiBanca({ inputDeposito, newBanca });
    }
    _saquei = () => {
        const { inputSaque } = this.state;
        const { saqueiBanca, newBanca } = this.props;
        saqueiBanca({ inputSaque, newBanca });
    }
    _zerar = async () => {
        const { zerarDadosCalculos } = this.props;
        await zerarDadosCalculos();
    }
    _recuperaNome = () => {
        const { recuperaNome, newEmail } = this.props;
        let inputEmail = newEmail;
        recuperaNome({inputEmail});
    }
    render(){
        const { newNome, newBanca, bancaInicial, newLucro, newLucroMensal, newLucroSemanal, newLucroDiario } = this.props;
        const { inputDeposito, inputSaque } = this.state;
        let NewBanca = newBanca.toFixed(2);
        let NewLucro = newLucro.toFixed(2);
        let NewLucroMensal = newLucroMensal.toFixed(2);
        let NewLucroSemanal = newLucroSemanal.toFixed(2);
        let NewLucroDiario = newLucroDiario.toFixed(2);
            return (
                <div class="main">
                    <Header />                    
                    <div className="content">
                            <h2>Olá { newNome }, tenha uma visão geral de seu Trading System!</h2>
                            { this._recuperaNome() }
                            <div className="content-dados">
                                <div className="flex-container">
                                    <div>
                                        <h2>R${ bancaInicial } </h2>
                                        <p>Banca Inicial</p>
                                    </div>
                                    <div>
                                        <h2>R${ NewBanca } </h2>
                                        <p>Saldo em Corretora</p>
                                    </div>
                                    <div>
                                        <h2>R${ NewLucro } </h2>
                                        <p>Lucro Total Líquido</p>
                                    </div>                                     
                                </div>
                                <div className="flex-container">
                                    <div>
                                        <h2>R${ NewLucroDiario } </h2>
                                        <p>Lucro Diário (EM BREVE)</p>
                                    </div>
                                    <div>
                                        <h2>R${ NewLucroSemanal } </h2>
                                        <p>Lucro Semanal (EM BREVE)</p>
                                    </div>
                                    <div>
                                        <h2>R${ NewLucroMensal } </h2>
                                        <p>Lucro Mensal (EM BREVE)</p>
                                    </div>
                                </div>

                            </div>
                            <h3> Preencha o campo abaixo para atualizar o valor de sua banca! </h3>
                            <div className="flex-container">
                                <div>
                                    <input 
                                        type="number"
                                        class='banca'
                                        value={inputDeposito}
                                        min="0"
                                        max="999999"
                                        onChange={this.inputChangeDeposito} />
                                    <input 
                                        class='atualizaBanca'
                                        value='Depositei'
                                        type='submit'
                                        onClick={this._depositei} />
                                </div>
                                <div>
                                    <input 
                                        type="number"
                                        class='banca'
                                        value={inputSaque}
                                        min="0"
                                        max="999999"
                                        onChange={this.inputChangeSaque} />
                                    <input 
                                        class='atualizaBanca'
                                        value='Saquei'
                                        type='submit'
                                        onClick={this._saquei} />
                                </div>
                            </div>
                        <div>
                            <input 
                                className='zerar'
                                value='Zerar Lucros'
                                type='submit'
                                onClick={this._zerar}
                            />
                        </div>
                    </div>
                    <Footer />
                </div>            
            );
        }
    }

    const mapStateToProps = state => ({
        newNome:            state.CalculosReducers.newNome,
        newEmail:            state.CalculosReducers.newEmail,
        bancaInicial:       state.CalculosReducers.bancaInicial,
        newBanca:           state.CalculosReducers.newBanca,
        newLucro:           state.CalculosReducers.newLucro,
        newLucroMensal:     state.CalculosReducers.newLucroMensal,
        newLucroSemanal:    state.CalculosReducers.newLucroSemanal,
        newLucroDiario:     state.CalculosReducers.newLucroDiario,
    })
    const mapDispatchToProps = dispatch => bindActionCreators({        
        appCarregando,
        depositeiBanca,
        saqueiBanca,
        zerarDadosCalculos,
        recuperaNome,
    }, dispatch);
    export default connect(mapStateToProps, 
        mapDispatchToProps
    )(Teste); 