import React from 'react'
import './style.css'
import  logo  from '../../img/logoC.png'
import {connect} from 'react-redux'
import { bindActionCreators } from 'redux'
import { 
    atualizaBancaInicial,
    AlteraNome
 } from '../../redux/actions'
 import { Redirect } from 'react-router-dom'
import Loading from '../loading';

class Inicial extends React.Component {
    state = {
        inputNome: '',
        inputBancaInicial: '',
        redirect: '',
    }
    inputChangeNome = event => {
        this.setState({ inputNome: event.target.value })
    }
    inputChangeBanca = event => {
        this.setState({ inputBancaInicial: event.target.value })
    }
    _atualizaDados = async () => {
        const { AlteraNome, atualizaBancaInicial } = this.props;
        const { inputNome, inputBancaInicial  } = this.state;
        await atualizaBancaInicial({ inputBancaInicial });
        await AlteraNome({ inputNome });

        if(inputBancaInicial !== '' && inputNome !== ''){
            this.setState({ redirect: 'Loading' });
            setTimeout( () => {
                this.setState({ redirect: 'OK' })
            }, 2000 )       
        }else{
            alert('Para continuar, preencha os campos de Nome e Banca!')
        }
    }
    render(){
        return ( 
            <div className="inicial">
            <div className='content'>
                <div className='boxImg'>
                    <img src={logo} />
                </div>
                
                <div className='box'>
                    <h3>Como deseja ser chamado?</h3>
                    <input 
                        type='text'
                        placeholder='Exemplo: João da Silva'
                        onChange={this.inputChangeNome}
                    />
                </div>

                <div className='box'>
                    <h3>Informe um valor inicial para usar nossa calculadora!</h3>
                    <p><i>(Não insira pontos ou vírgulas)</i></p>                    
                    <input 
                        type='number'
                        placeholder='Exemplo: R$1.000,00'
                        onChange={this.inputChangeBanca}
                    />
                </div>

                {
                this.state.redirect === 'Loading'
                ?   <Loading />
                :   <div className='box'>
                        <input 
                            className='botaoAction'
                            type='submit'
                            value='Continuar'
                            onClick={this._atualizaDados}
                        />
                    </div>
                }
            </div>
            { this.state.redirect === 'OK'
                ? <Redirect to="/main"></Redirect>
                : null }
        </div>
        );
    }
}


const mapStateToProps = state => ({
    newNome:  state.CalculosReducers.newNome,
    bancaInicial:  state.CalculosReducers.bancaInicial,
})
const mapDispatchToProps = dispatch => bindActionCreators({
    atualizaBancaInicial,
    AlteraNome
}, dispatch);
export default connect(mapStateToProps, 
    mapDispatchToProps
)(Inicial); 