export const ATUALIZA_ORDEM = "ATUALIZA_ORDEM";
export const atualizaOrdem = value => ({
    type: ATUALIZA_ORDEM,
    newOrdem: value,
});

export const ATUALIZA_PAYOUT = "ATUALIZA_PAYOUT";
export const atualizaPayout = value => ({
    type: ATUALIZA_PAYOUT,
    newPayout: value
});

export const ATUALIZA_STOPLOSS = "ATUALIZA_STOPLOSS";
export const atualizaStoploss = value => ({
    type: ATUALIZA_STOPLOSS,
    newStoploss: value
});

export const DEPOSITEI_BANCA = "DEPOSITEI_BANCA";
export const depositeiBanca = ({inputDeposito, newBanca}) => ({
    type: 'DEPOSITEI_BANCA',
    newBanca: parseFloat(inputDeposito)+parseFloat(newBanca),
});

export const SAQUEI_BANCA = "SAQUEI_BANCA";
export const saqueiBanca = ({inputSaque, newBanca}) => ({
    type: 'SAQUEI_BANCA',
    newBanca: parseFloat(newBanca)-parseFloat(inputSaque),
});

export const BANCA_INICIAL = "BANCA_INICIAL";
export const atualizaBancaInicial = ({inputBancaInicial}) => ({
    type: 'BANCA_INICIAL',
    bancaInicial: inputBancaInicial
});

export const ATUALIZA_BANCA = "ATUALIZA_BANCA";
export const atualizaBanca = ({inputValue, newBanca}) => ({
    type: 'ATUALIZA_BANCA',
    newBanca: parseFloat(newBanca)+parseFloat(inputValue),
});

export const ATUALIZA_LUCRO = "ATUALIZA_LUCRO";
export const atualizaLucro = ({inputValue, newLucro}) => ({
    type: 'ATUALIZA_LUCRO',
    newLucro: parseFloat(inputValue) + parseFloat(newLucro)
});

export const ATUALIZA_LUCRO_MENSAL = "ATUALIZA_LUCRO_MENSAL";
export const atualizaLucroMensal = ({ inputLucroMensal, newLucroMensal }) => ({
    type: 'ATUALIZA_LUCRO_MENSAL',
    newLucroMensal: parseFloat(newLucroMensal)+parseFloat(inputLucroMensal),
});

export const ATUALIZA_LUCRO_SEMANAL = "ATUALIZA_LUCRO_SEMANAL";
export const atualizaLucroSemanal = ({lucroSemanal, newBanca}) => ({
    type: 'ATUALIZA_LUCRO_SEMANAL',
    newLucroSemanal: parseFloat(newBanca)+parseFloat(lucroSemanal),
});

export const ATUALIZA_LUCRO_DIARIO = "ATUALIZA_LUCRO_DIARIO";
export const atualizaLucroDiario = ({lucroDiario, newBanca}) => ({
    type: 'ATUALIZA_LUCRO_DIARIO',
    newLucroDiario: parseFloat(newBanca)+parseFloat(lucroDiario),
});

export const ZERAR_DADOS_CALCULOS = "ZERAR_DADOS_CALCULOS";
export const zerarDadosCalculos = () => ({
    type: 'ZERAR_DADOS_CALCULOS',
});


export const GET_HISTORICO = "GET_HISTORICO";
export const getHistorico = registro => ({
    type: 'GET_HISTORICO',
    registro
});

export const UPDATE_HISTORICO = "UPDATE_HISTORICO";
export const updateHistorico = ({ id, criadoEm, inputRegistro }) => ({
    type: 'UPDATE_HISTORICO',
    id: id, 
    criadoEm: criadoEm,
    registro: inputRegistro
});


