import { 
    //CALCULOS
    ATUALIZA_ORDEM,
    ATUALIZA_PAYOUT,
    ATUALIZA_STOPLOSS,
    DEPOSITEI_BANCA,
    SAQUEI_BANCA,
    ATUALIZA_LUCRO_MENSAL,
    ATUALIZA_BANCA,
    BANCA_INICIAL,
    ATUALIZA_LUCRO,
    ZERAR_DADOS_CALCULOS,
    UPDATE_HISTORICO,

    //USUARIO
    CADASTRO_USUARIO_ERRO,
    CADASTRO_USUARIO_SUCESSO,
    AUTENTICA_USUARIO_ERRO,
    AUTENTICA_USUARIO_SUCESSO,
    CLICK_UPDATE_EMAIL,
    CLICK_UPDATE_NOME,
    CLICK_UPDATE_SENHA,
    RECUPERA_SENHA_SUCESSO,
    RECUPERA_SENHA_ERRO,

    //APP
    APP_SAIR,
    APP_CARREGANDO,
    RECUPERA_NOME,
 } from '../actions'

 
const INITIAL_STATE = {
    //APP
    carregando: false,
    redirect:'',

    //USUARIO
    newNome: '',
    newEmail: '',
    newSenha: '',
    erroLogin:'',
    erroCadastro: '',
    erroRecuperaSenha:'',
    sucessoRecuperaSenha:'',


    //CALCULOS
    bancaInicial:0,
    newBanca:0,
    newPayout:0,
    newStoploss:0,
    newOrdem:0,
    retorno:0,
    resultado:0,
    newCopia:'',
    newLucro:0,
    newLucroMensal:0,
    newLucroSemanal:0,
    newLucroDiario:0,
    newDeposito:0,
    newSaque:0,
    
    id: Math.floor(Math.random() * 100000 + 100000),
    criadoEm: Date().toLocaleString(),
    registro:0,
}

const CalculosReducers = (state = INITIAL_STATE, action) => {
    console.log(action);
    switch(action.type){
        // APP
        case APP_CARREGANDO:
            return {
                ...state,
                carregando: action.carregando
            } 
        case APP_SAIR:
            return {
                ...state,
                redirect: '',
            }
        
        // USER
        case CLICK_UPDATE_NOME:
            return {
              ...state,
              newNome: action.newNome,
            }
        case CLICK_UPDATE_SENHA:
            return {
              ...state,
              newSenha: action.newSenha
            }
        case CLICK_UPDATE_EMAIL:
            return {
              ...state,
              newEmail: action.newEmail
            }
        case CADASTRO_USUARIO_ERRO:
            return {
                ...state,
                erroCadastro: action.payload,
            }
        case CADASTRO_USUARIO_SUCESSO:
            return {
                ...state,
                redirect: 'Login',
            }
        case AUTENTICA_USUARIO_ERRO:
            return {
                ...state,
                erroLogin: action.payload,
            } 
        case AUTENTICA_USUARIO_SUCESSO:
            return {
                ...state,
                redirect: 'Principal'
            }
        case RECUPERA_SENHA_SUCESSO:
            return {
                ...state,
                sucessoRecuperaSenha: action.payload,
            }
        case RECUPERA_SENHA_ERRO:
            return {
                ...state,
                erroRecuperaSenha: action.payload
            }

        // CALCULOS
        case ATUALIZA_ORDEM:
            return {
              ...state,
              newOrdem: action.newOrdem,
            }
        case ATUALIZA_PAYOUT:
            return {
              ...state,
              newPayout: action.newPayout,
            }
        case ATUALIZA_STOPLOSS:
            return {
              ...state,
              newStoploss: action.newStoploss,
            }
        case DEPOSITEI_BANCA:
            return {
              ...state,
              newBanca: action.newBanca,
            }
        case SAQUEI_BANCA:
            return {
              ...state,
              newBanca: action.newBanca,
            }
        case ATUALIZA_BANCA:
            return {
              ...state,
              newBanca: action.newBanca,
            }
        case BANCA_INICIAL:
            return {
              ...state,
              bancaInicial: action.bancaInicial,
            }
        case ATUALIZA_LUCRO_MENSAL:
            return {
              ...state,
              newLucroMensal: action.newLucroMensal,
            }
        case ATUALIZA_LUCRO:
            return {
              ...state,
              newLucro: action.newLucro,
            }
        case ZERAR_DADOS_CALCULOS:
            return {
                ...state,
                newLucro:0,
                newLucroMensal:0,
                newLucroSemanal:0,
                newLucroDiario:0,
            }
        case UPDATE_HISTORICO:
            return {
                ...state,
                id: action.id, 
                criadoEm: action.criadoEm,
                registro: action.registro
            }
        }
    return state;
}
 
export default CalculosReducers;